import { collection, getDocs } from "firebase/firestore";
import { useEffect, useState } from "react";
import EventCard from "../../components/EventCard/EventCard";
import { db } from "../../firebase";


const EventsPage = () => {

  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  let colRef = collection(db, "events");
  let cards = [];
  let xd = 1;
  
  useEffect(() => {
    xd = xd +1;
    console.log(xd);
    
    if(xd === 3){
      let events = [];
      setLoading(true);
      getDocs(colRef).then((snapshot) => {
        snapshot.docs.forEach((docs, idx) => {
          events.push({ ...docs.data(), id: docs.id });
        });
        setLoading(false);
        setData(events);
        //console.log(events);
      });
    }
    
  }, []);

  return (
    <div className="event-page__main-container">
      {loading ? (
        "Cargando"
      ) : (
        data.map((elem)=>{
          return <EventCard title={elem.title} description={elem.description} idElem={elem.id}/>
        })
      )}
    </div>
  );
};

export default EventsPage;
